nav{
	display:flex;
	align-items: center;
	justify-content: space-around;
	padding-top: 3px;
	padding-left: 1%;
	padding-right: 2%;
}

nav a{
  text-decoration: none;
  color: black;
  font-size: 25px;
}

nav a:hover{
  border: 2px solid;
  background-color: #2691d9;
  font-size: 28px;
  transition: transform .4s;

}

.logo{
  color: black;
  border: 5px double;
  padding-left: 4px;
  padding-right: 4px;
  font-size: 30px;
}